import React, { Component } from 'react';
import Link from 'next/link'


//CSS

const containerNav = {
  fontFamily: 'Muli',
  backgroundColor: '#80808099',
  margin: '0',
  padding: '0',
  width: '100%',
  height: '35px',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center'
}

const links = {
  color: 'black',
  textDecoration: 'none',
  marginRight: '10px',
  marginLeft: '10px'
}


class Nav extends Component{
  render(){
    return(
      <div style={containerNav}>
        <Link href="/">
          <a style={links}>Home</a>
        </Link>
        <Link href="/test">
          <a style={links}>Test</a>
        </Link>
      </div>
    )
  }
}


export default Nav;



